export default{
	data(){
		return{
			uid:'',
			token:'',
			data:''
		}
	},
	mounted(){
		this.init()
	},
	methods:{
		init(){
			var userInfo=uni.getStorageSync('userInfo')
			if(userInfo!='' & userInfo!=null & userInfo!=undefined){
				this.uid=userInfo.uid
				this.token=userInfo.token
				this.getWorkBench()
			}
		},
		toOrderList(type){
			uni.navigateTo({
				url:'./operating/orderList?active='+type
			})
		},
		toggletab(w){
			this.$emit('toggletab',w)
		},
		toSetting(){
			var that=this
			uni.$on('updateOperating',function(){
				that.getWorkBench()
			})
			uni.navigateTo({
				url:'./operating/setting'
			})
		},
		toGoods(){
			uni.navigateTo({
				url:'./operating/goods'
			})
		},
		getWorkBench(){
			this.$loading()
			var that=this
			var url='&r=api.myshop.workbench'
			var params={
				uid:this.uid,
				token:this.token
			}
			  this.$apiPost(url,params).then((res) =>{
				  uni.hideLoading()
				that.data=res.data	
				that.$forceUpdate()
			  })
		}
	}
}
